import {
	loginurl,
	myrequest,
	uidurl
} from '../../api.js';
export default {
	data() {
		return {
			mobile: '',
			password: '',
		}
	},
	onLoad() {

	},
	methods: {
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
			if (key == 'password') {
				if (this['mobile'].length != 11) {
					uni.showToast({
						title: '电话长度不等于11位,请重新输入',
						icon: 'none'
					})
				}
			}
		},
		toRegist() {
			uni.navigateTo({
				url: './register',
			});
		},
		async toLogin() {
			const {
				mobile,
				password
			} = this;
			const sendData = {
				name: mobile,
				password
			};
			const uid = await myrequest(uidurl,'POST',{ name : this.mobile })
			const result = await myrequest(loginurl, 'POST', sendData);
			if (result['respCode'] == 0) {
				uni.setStorage({
					key: 'mobile',
					data: this.mobile,
				});
				uni.setStorage({
					key: 'uid',
					data: uid["data"],
				});
				uni.switchTab({
					url: '../index/index',
				});
			} else if (result['respCode'] == 1) {
				uni.showToast({
					title: '请检查账号密码',
					icon: 'none',
				});
			}
		}
	},

}
